.gallery {
    background-color: $color-grey-light-1;
    grid-column: full-start / full-end;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(7, 5vw);
    gap: 1.5rem;
    padding: 1.5rem;



    &__item {
        &--1 {
            grid-row: 1/span 2;
            grid-column: 1/span 2;
        }

        &--2 {
            grid-row: 1/4;
            grid-column: 3/6;
        }

        &--3 {
            grid-row: 1/span 2;
            grid-column: 6/7;
        }

        &--4 {
            grid-row: 1/span 2;
            grid-column: 7/9;
        }

        &--5 {
            grid-row: 3/6;
            grid-column: 1/3;
        }

        &--6 {
            grid-row: 4/6;
            grid-column: 3/5;
        }

        &--7 {
            grid-row: 4/5;
            grid-column: 5/6;
        }

        &--8 {
            grid-row: 3/5;
            grid-column: 6/8;
        }

        &--9 {
            grid-row: 3/6;
            grid-column: 8/9;
        }

        &--10 {
            grid-row: 6/8;
            grid-column: 1/2;
        }

        &--11 {
            grid-row: 6/8;
            grid-column: 2/4;
        }

        &--12 {
            grid-row: 6/8;
            grid-column: 4/5;
        }

        &--13 {
            grid-row: 5/8;
            grid-column: 5/8;
        }

        &--14 {
            grid-row: 6/8;
            grid-column: 8/9;
        }




    }

    &__img {
        // Esta propiedad esta hecha para llenar las celdas con imagenes ignorando sus proporciones
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;

    }
}